import React from "react";
import { Beer } from "../types/Beer";
import { Link } from "react-router-dom";

interface BeerCardProps {
  beer: Beer;
}

const BeerCard: React.FC<BeerCardProps> = ({ beer }) => {
  return (
    <div className="bg-white shadow-md rounded-lg overflow-hidden p-4 flex flex-col">
      <img
        src={beer.image}
        alt={beer.name}
        className="w-full h-48 object-cover rounded-t-lg mb-4"
      />
      <h3 className="text-lg font-semibold mb-2">{beer.name}</h3>
      <p className="text-gray-600 mb-2">{beer.style}</p>
      <p className="text-gray-600 mb-2">{beer.abv}% ABV</p>
      <p className="text-gray-800 font-bold mb-4">EUR {beer.price}</p>
      <Link
        to={`/beers/${beer.id}`}
        className="bg-blue-500 text-white py-2 px-4 rounded-md text-center hover:bg-blue-600 transition"
      >
        View Details
      </Link>
    </div>
  );
};

export default BeerCard;
