import React, { useEffect, useState } from "react";
import { useBeerStore } from "../store/beerStore";
import { fetchBeers } from "../api/beerService";
import BeerGrid from "../components/BeerGrid";
import BeerFilter from "../components/BeerFilter";

const LandingPage: React.FC = () => {
  const [error, setError] = useState<string | null>(null);
  const { beers, setBeers, filters, setFilter, clearFilters } = useBeerStore();

  useEffect(() => {
    const loadBeers = async () => {
      const data = await fetchBeers();
      setBeers(data);
    };
    loadBeers();
  }, [setBeers]);

  const filteredBeers = beers.filter((beer) => {
    return (
      (filters.brand === "" || beer.brand.includes(filters.brand)) &&
      (filters.style === "" || beer.style.includes(filters.style)) &&
      (filters.abv === "" || beer.abv >= parseFloat(filters.abv)) &&
      (filters.price === "" || beer.price <= parseFloat(filters.price))
    );
  });


  useEffect(() => {
    const loadBeers = async () => {
      try {
        const data = await fetchBeers();
        if (data.length === 0) {
          setError('No beers available');
        } else {
          setBeers(data);
        }
      } catch (err) {
        setError('Failed to fetch beers');
      }
    };
    loadBeers();
  }, [setBeers]);

  if (error) {
    return <div className="text-center text-red-600">{error}</div>;
  }

  return (
    <div className="container mx-auto p-4">
      <h1 className="text-4xl font-bold text-center my-6">
        Craft Beer Emporium
      </h1>
      <BeerFilter setFilter={setFilter} clearFilters={clearFilters} />
      <BeerGrid beers={filteredBeers} />
    </div>
  );
};

export default LandingPage;
